body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #48b1d1 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar-light .navbar-brand {
  color: rgba(0, 0, 0, 0.72) !important;
  font-weight: 700;
}

.my-title{
  padding-bottom: 20px;
    font-size: 25px;
    color: white;
    font-family: cursive;
    font-weight: 400;
}
.my-title span{
  border-bottom: 1px solid white;
}
.my-logo-m{
  background: #48b1d1;
    border-top-left-radius: 6px;
    border-bottom-right-radius: 6px;
    color: white;
    font-weight: 400;
    padding: 0px 6px 3px 6px;
}
.my-img{
  /* height:381px; */

  height: 500px;
  width: 500px;
  margin: auto;
  background:url("./images/1.jpeg");
  clip-path:circle(50% at 50% 50%);
  background-size: cover;
  background-repeat: no-repeat;
}
.my-img2{
  background:url("./images/2.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 295px;
  width: 295px;
  margin: auto;
  border: 1px solid #1c6e84;
}
.social_icone{
  list-style: none;
  position: absolute;
  left:0;
  top:290px;
  margin: 0px;
  padding: 0px;
  padding-left: 17px;
}
.social_icone li {
  margin-right: 5px;
  float: left;
}

.social_icone li > i {
 font-size:23px;
 color:white;
}
.skill .row .text-center > i{
  font-size:100px;
}
.my-footer{
  height: 100px;

    color: white;
    padding-top: 12px;
    position: relative;
    bottom:0
}

@media screen and (max-width:768px){

  .headline{
    padding-bottom: 35px;
  }

  .headline h1{
    font-size:1.8rem;
  }

  .my-img{
    height:307px;
    width:307px;
    margin:auto;
  }
  .social_icone{
    top:250px;
  }
  
  .social_icone li {
    margin-bottom: 5px;
    float: none;
  }
  .skill{
   height:780px !important;
  }
}